import React, { useRef, useEffect, useState } from "react";
// import ReactSlider from "react-slider";
import Slider, { Range } from "rc-slider";
import "rc-slider/assets/index.css";

export default function ImageEffect() {
  const canvasRef = useRef(null);
  const buttonRef = useRef(null);
  const imageRef = useRef(null);
  const [firstLoad, setFirstLoad] = useState(true);
  const [pixelSize, setPixelSize] = useState(5);

  const handleSliderChange = (value) => {
    setPixelSize(value);
    // Do anything else you want when the slider value changes
  };

  useEffect(() => {
    const canvas = canvasRef.current;
    const ctx = canvas.getContext("2d");
    canvas.width = window.innerWidth;
    canvas.height = window.innerHeight;

    class Particle {
      constructor(effect, x, y, color) {
        this.effect = effect;
        if (firstLoad) {
          this.x = Math.random() * this.effect.width;
          this.y = Math.random() * this.effect.height;
        } else {
          this.x = Math.floor(x);
          this.y = Math.floor(y);
        }
        this.originX = Math.floor(x);
        this.originY = Math.floor(y);
        this.color = color;
        this.size = this.effect.gap;
        this.vx = 0;
        this.vy = 0;
        this.ease = 0.05;
        this.friction = 0.5;
        this.dx = 0;
        this.dy = 0;
        this.distance = 0;
        this.force = 0;
        this.angle = .5;
      }
      draw(context) {
        context.fillStyle = this.color;
        context.fillRect(this.x, this.y, this.size, this.size);
      }
      update() {
        this.dx = this.effect.mouse.x - this.x;
        this.dy = this.effect.mouse.y - this.y;
        this.distance = Math.sqrt(this.dx * this.dx + this.dy * this.dy);
        this.force = -this.effect.mouse.radius / this.distance;

        if (this.distance < this.effect.mouse.radius) {
          this.angle = Math.atan2(this.dy, this.dx);
          this.vx += this.force * Math.cos(this.angle);
          this.vy += this.force * Math.sin(this.angle);
        }

        this.x +=
          (this.vx *= this.friction) + (this.originX - this.x) * this.ease;
        this.y +=
          (this.vy *= this.friction) + (this.originY - this.y) * this.ease;
      }
      warp() {
        this.x = Math.random() * this.effect.width;
        this.y = Math.random() * this.effect.height;
        this.ease = 0.01;
      }
    }
    class Effect {
      constructor(width, height) {
        this.width = width;
        this.height = height;
        this.particlesArray = [];
        // this.image = document.getElementById("image1");
        this.image = imageRef.current;
        this.centerX = this.width / 2;
        this.centerY = this.height / 2;
        this.x = this.centerX - this.image.width;
        this.y = this.centerY - this.image.height;
        this.gap = pixelSize;
        this.mouse = {
          radius: 100,
          x: undefined,
          y: undefined,
        };
        window.addEventListener("mousemove", (event) => {
          this.mouse.x = event.x;
          this.mouse.y = event.y;
        });
      }
      init(context) {
        context.drawImage(
          this.image,
          this.x,
          this.y,
          this.image.width * 2,
          this.image.height * 2
        );
        const pixels = context.getImageData(0, 0, this.width, this.height).data;
        for (let y = 0; y < this.height; y += this.gap) {
          for (let x = 0; x < this.width; x += this.gap) {
            const index = (y * this.width + x) * 4;
            const red = pixels[index];
            const green = pixels[index + 1];
            const blue = pixels[index + 2];
            const alpha = pixels[index + 3];
            const color = `rgb(${red}, ${green}, ${blue})`;

            if (alpha > 0) {
              this.particlesArray.push(new Particle(this, x, y, color));
            }
          }
        }
      }

      draw(context) {
        for (let i = 0; i < this.particlesArray.length; i++) {
          this.particlesArray[i].draw(context);
        }
      }
      update() {
        for (let i = 0; i < this.particlesArray.length; i++) {
          this.particlesArray[i].update();
        }
      }
      warp() {
        for (let i = 0; i < this.particlesArray.length; i++) {
          this.particlesArray[i].warp();
        }
      }
    }

    const effect = new Effect(canvas.width, canvas.height);
    effect.init(ctx);
    function animate() {
      ctx.clearRect(0, 0, canvas.width, canvas.height);
      effect.draw(ctx);
      effect.update();
      requestAnimationFrame(animate);
    }
    animate();

    //warps the image
    const button = buttonRef.current;
    button.addEventListener("click", () => {
      effect.warp();
    });
    setFirstLoad(false);
    return () => {};
  }, [pixelSize]);

  return (
    <div style={{ flex: 1 }}>
      <button
        onClick={() => console.log("clicked")}
        style={{ position: "absolute", zIndex: 100 }}
        ref={buttonRef}
        id="warp"
      >
        Warp
      </button>
      <div style={{ position: "absolute", top: "100px", left: "10px" }}>
        <h2 style={{color:'white'}}>Pixel Size {pixelSize}</h2>
        <Slider
          min={3}
          max={10}
          value={pixelSize}
          onChange={handleSliderChange}
          style={{
            zIndex: 100,
            width: "100px",
          }}
        />
      </div>
      <canvas
        ref={canvasRef}
        style={{
          width: "100%",
          height: "100vh",
          backgroundColor: "black",
        }}
        className="scene"
      ></canvas>
      <img
        id="image1"
        ref={imageRef}
        src="data:image/png;base64,"
      />
      </div>
  );
}
